html {
  background-color: #26323a;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

@keyframes appear {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes underline_nav {
  0% {
    width: 0;
  }
  100% {
    opacity: 1;
    width: 80%;
    transform: translateX(-50%);
  }
}
@keyframes hover_nav {
  from {
    opacity: 0.6;
  }
  to {
    opacity: 1;
  }
}
@keyframes photo_hover {
  from {
    filter: grayscale(1);
    transform: scale(1);
  }
  to {
    filter: grayscale(0);
    transform: scale(1.05);
  }
}
@keyframes fontColorChange {
  from {
    color: #ff0000;
  }
  to {
    color: #ffa500;
  }
}
@font-face {
  font-family: "Neucha";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
