/* Общие блоки */
.button {
  border: 2px solid #ffffff;
  background-color: #26323a;
  color: #ffffff;
  font-size: 30px;
  box-sizing: border-box;
  cursor: pointer;
}
.button:hover, .button:focus {
  font-weight: bold;
  border-radius: 10px;
  background-color: #ffffff;
  color: #26323a;
  transition: border-radius 0.5s ease, background-color 0.3s ease;
}
.button:active {
