/* modal */
.modal {
  display: none;
}

.modal-open {
  height: 100vh;
  overflow: hidden;
}

.modal--opened {
  margin: auto;
  padding: 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(84, 84, 84, 0.95);
  width: 100%;
  height: 100%;
  min-width: 0;
  z-index: 101;
  animation: appear 0.5s ease;
}

.modal__photo-container {
  margin: auto;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  transition: all 0.5s ease;
}

.modal__wrapper {
  padding: 0;
  margin: auto;
  max-width: 95%;
  width: 95%;
  max-height: 95%;
  height: 95%;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
}

.modal__wrapper .swiper-pagination {
  bottom: 10;
}

.modal__wrapper .swiper-pagination-fraction {
  line-height: 26px;
  font-size: 30px;
  font-family: "Comforter", "Arial", sans-serif;
  color: #ffffff;
  z-index: 120;
}

.modal__photo-wrapper {
  max-width: 95vw;
  width: 100%;
  max-height: 95vh;
  height: 100%;
}

.modal__photo {
  margin: auto;
  max-width: 95vw;
  width: 100%;
  max-height: 95vh;
  height: 100%;
  object-fit: contain;
}

.modal__close {
  margin: 0;
  padding: 0;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 25px;
  right: 0;
  content: "";
  border: none;
  background-color: transparent;
  z-index: 100;
}
@media (min-width: 768px) {
  .modal__close {
    top: 0;
    right: 0;
  }
}
@media (min-width: 1024px) {
  .modal__close {
    width: 30px;
    height: 30px;
    right: 0;
  }
}
.modal__close:hover {
  opacity: 0.7;
  cursor: pointer;
}
.modal__close:active {
  opacity: 0.3;
}

.modal__close-icon {
  width: inherit;
  height: inherit;
  color: #ffffff;
}

.modal__button-container {
  width: 80%;
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 120;
}

.modal__button {
  margin: 0;
  padding: 0;
  width: 50px;
  height: 50px;
  background-color: transparent;
  border: none;
  text-align: center;
  box-sizing: border-box;
  color: #ffffff;
  opacity: 0.3;
}
.modal__button:hover, .modal__button:focus {
  border: 1px solid #ffffff;
  border-radius: 3px;
  cursor: pointer;
  opacity: 1;
}
.modal__button:active {
  opacity: 0.5;
}

.modal__icon {
  font-size: 20px;
  line-height: 20px;
  width: 100%;
  height: 100%;
}

